import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Layout from '../components/layout';
import ServiceCard from '../components/serviceCard';



import { FaLightbulb, FaCode, FaChartLine  } from 'react-icons/fa';

const IndexPage = () => {

    const scrollToSection = (id) => {
        const element = document.getElementById(id);
        if (element) {
            const headerHeight = document.getElementById('main-header').offsetHeight;
            const elementPosition = element.getBoundingClientRect().top + window.pageYOffset;
            const offsetPosition = elementPosition - headerHeight - 20;
            window.scrollTo({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }
    };

    return (
        <Layout>
     
            <section id="home" className="hero-section image-hero-2 d-flex align-items-center justify-content-center text-white text-center">
                <Container>
                    <h1 className="display-1 fw-bold mb-4">Biometrics for Art (ArtMetrics)</h1>
                    <p className="display-5 lead mb-5">Milan (Italy), 11th September 2024</p>
                </Container>
            </section>

      
            <section id="description" className="py-5">
                <Container>
                    <h2 className="text-center mb-5">Description</h2>
                    <Row className="align-items-center">
                        
                        <Col md={12}>                           
                            <p>The Biometrics for Arts (ArtMetrics) workshop aims to explore the intersection of biometrics, computer vision and the arts to provide a more nuanced understanding of an artwork's provenance and maker(s). In the same way as Biometrics serves as a tool for person identification from unique phenotypic or behavioural traits, Biometrics for Arts aims at artist recognition from unique attributes detected on works of art, thus fostering dialogue between engineers, computer scientists, heritage scientists, conservators, and art historians. This workshop will showcase innovative applications of computer vision in the visual art domain, emphasizing the role of technology in supporting conservation practices and enhancing the management of museum and private collections. Key topics include pattern recognition in works of art, AI-driven artistic generation, AI-driven analysis of multimodal imaging data of works of art, and digital restoration of different media. ArtMetrics seeks to inspire interdisciplinary collaboration, highlighting how computer vision can both interpret and enhance the diverse world of art.</p>
                        </Col>
                    </Row>
                </Container>
            </section>

            
      
             <section id="location" className="bg-light py-5">
                <Container>
                    <h2 className="text-center mb-5">Location</h2>
                    <Row className="align-items-center">
                        <Col md={6} className="mb-4 mb-md-0">
                        <iframe
                          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2451.657378400608!2d5.168962277755974!3d52.08596667195066!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47c66f5054136f89%3A0xd734a1a1fad45b36!2sUniversidad%20de%20Utrecht!5e0!3m2!1ses!2ses!4v1748607066860!5m2!1ses!2ses"
                          width="100%" 
                          height="450"
                          style={{ border: 0 }} 
                          allowFullScreen=""
                          loading="lazy"
                          referrerPolicy="no-referrer-when-downgrade"
                      ></iframe>                       
                       </Col>
                        <Col md={6}>
                            <p className="lead">The workshop is located in</p>
                            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p> 

                            <span>Number <br/>
                            Location<br/>
                            ST, 13, 20149 D MI, Netherland.</span>

                        </Col>
                    </Row>
                </Container>
            </section>

          
            <section id="call" className="py-5">
                <Container>
                    <h2 className="text-center mb-5">Call for Papers</h2>
                    <div>
                      <p>Lorem ipsum dolor sit amet consectetur adipiscing elit nibh hendrerit rutrum, tristique auctor aptent tortor elementum diam fringilla euismod tempor. Vestibulum inceptos sagittis aptent non id velit vitae, aenean augue porta eros at lacinia sapien litora, aliquet rutrum cras eleifend fames posuere. Ornare cubilia in libero montes mi pretium lobortis, imperdiet feugiat lacus pellentesque non porta sapien dui, magna hac gravida vitae inceptos mollis.</p>
                      <p>Mus eleifend eu cum nullam morbi nulla massa commodo urna, dignissim fusce ridiculus molestie ligula orci varius netus, et ullamcorper duis dui litora nibh imperdiet blandit. Sociosqu aptent et ornare ultricies netus ut sociis, a vel habitant urna dui mi suscipit, integer hendrerit felis nam duis himenaeos. Imperdiet primis leo ultricies est litora egestas integer, fringilla posuere convallis eu feugiat nisl mollis tincidunt, quisque magna ac potenti habitant tempus.</p>
                      <a href="https://www.google.com" target='_blank'>Link to ...</a>
                    </div>
                </Container>
            </section>



            <section id="info" className="bg-light  py-5">
                <Container>
                    <h2 className="text-center mb-5">Custom Cards</h2>
                    <Row xs={1} md={3} className="g-4">
                        <ServiceCard
                            icon={FaLightbulb}
                            title="Card 1"
                            description="Description card 1"
                        />
                        <ServiceCard
                            icon={FaCode}
                            title="Card 2"
                            description="Description card 2"
                        />
                        <ServiceCard
                            icon={FaChartLine}
                            title="Card 3"
                            description="Description card 3."
                        />
                    </Row>
                </Container>
            </section>

        
            

           
        </Layout>
    );
};

export default IndexPage;